import './App.css';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom"
import LandingPage from './components/Landing/LandingPage'
function App() {
  return (
    <Router>
      <div>
        <Routes>
          <h1>Hola pagina</h1>
          <Route exact path="/" component={LandingPage}/>
          {/* <Route path="/home" component={}/> */}
        </Routes>
      </div>
    </Router>
  );
}

export default App;
